.account {
  padding: 16px 0 48px;
  min-height: 100vh;
  @media (max-width: 1023px) {
    overflow: hidden;
  }
  @media (max-width: 767px) {
    padding-bottom: 32px;
  }
  &__title {
    @include h1;
    margin-bottom: 16px;
  }
  &__body {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    margin-top: 16px;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__content {
    grid-column: span 2;
    // @media (max-width: 767px) {
    //   overflow: hidden;
    // }
    &_wide {
      grid-column: span 3;
    }
  }
  &__box {
    display: flex;
    flex-direction: column;
    gap: $gap;
    padding: 16px;
    background-color: $white;
    border-radius: 24px;
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }
  &__subtitle {
    @include h3;
    line-height: 1.5;
  }
  &__info {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap 12px;
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__buttons {
    max-width: 456px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: $gap;
    @media (max-width: 767px) {
      max-width: none;
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__organization {
    display: none;
    &_active {
      display: flex;
      flex-direction: column;
      gap: $gap;
    }
  }
  &__address {
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    @media (max-width: 767px) {
      flex-wrap: nowrap;
      margin: 0 -16px;
      gap: 4px;
      overflow-x: auto;
      -ms-overflow-style: none;
      scrollbar-width: none;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
    &-new {
      display: none;
      &_active {
        display: flex;
        flex-direction: column;
        gap: $gap;
      }
    }
    &-item {
      width: 150px;
      height: 86px;
      flex-shrink: 0;
      padding: 8px;
      background-color: $light-grey;
      border-radius: 12px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
      gap: $gap;
      @media (max-width: 767px) {
        &:first-child {
          margin-left: 16px;
        }
        &:last-child {
          margin-right: 16px;
        }
      }
      &:is(a) {
        background-color: $light-blue-2;
        transition: all $transition;
        &:hover {
          background-color: $main-blue-hover;
          color: $white;
          svg {
            stroke: $white;
          }
        }
        &.account__address-item_active {
          background-color: $main-blue-hover;
          color: $white;
          svg {
            stroke: $white;
          }
        }
      }
    }
    &-suptitle {
      @include label;
      color: $grey;
    }
    &-title {
      margin-top: auto;
      @include text-small;
      letter-spacing: -.011em;
    }
    &-img {
      width: 24px;
      height: 24px;
      svg {
        width: 100%;
        height: 100%;
        stroke: $dark;
        stroke-width: 2px;
        transition: all $transition;
      }
    }
  }
  &__payment {
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    @media (max-width: 767px) {
      flex-wrap: nowrap;
      margin: 0 -16px;
      gap: 4px;
      overflow-x: auto;
      -ms-overflow-style: none;
      scrollbar-width: none;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
    &-item {
      width: 150px;
      height: 86px;
      flex-shrink: 0;
      padding: 8px;
      background-color: $light-grey;
      border-radius: 12px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
      gap: $gap;
      @media (max-width: 767px) {
        &:first-child {
          margin-left: 16px;
        }
        &:last-child {
          margin-right: 16px;
        }
      }
    }
    &-suptitle {
      @include label;
      color: $grey;
    }
    &-title {
      margin-top: auto;
      @include text-small;
      letter-spacing: -.011em;
    }
  }
  &__orders {
    margin-top: 16px;
    &-item {
      padding: 16px;
      background-color: $white;
      border-radius: 24px;
      &:not(:last-child) {
        margin-bottom: 16px;
      }
      &-info {
        display: flex;
        align-items: center;
        gap: 16px;
        margin-bottom: 16px;
        @media (max-width: 767px) {
          margin-bottom: 18px;
        }
      }
      &-title {
        @include h4;
      }
      &-date {
        @include text-normal;
        color: $grey;
      }
      &-label {
        min-height: 31px;
        display: flex;
        align-items: center;
        padding: 5px 10px;
        background-color: #F9F9F9;
        border-radius: 8px;
        @include text-normal;
        &s {
          display: flex;
          flex-wrap: wrap;
          gap: $gap;
          @media (max-width: 767px) {
            gap: 8px;
          }
        }
      }
      &-more {
        margin-top: 16px;
        height: 31px;
        display: inline-flex;
        align-items: center;
        gap: 8px;
        padding: 0 10px;
        background-color: $light-orange;
        border-radius: 8px;
        @include text-normal;
        color: $orange;
        svg {
          width: 16px;
          height: 16px;
          flex-shrink: 0;
          stroke: $orange;
          stroke-width: 2px;
          transform: rotate(90deg);
          transition: all $transition;
        }
        &_active {
          svg {
            transform: rotate(-90deg);
          }
        }
      }
      &-dropdown {
        display: none;
        padding-top: 16px;
      }
    }
    &-product {
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 8px;
      position: relative;
      @media (max-width: 767px) {
        display: block;
      }
      &:not(:last-child) {
        border-bottom: 1px solid #F1F1F1;
        margin-bottom: 16px;
      }
      &-img {
        width: 80px;
        height: 82px;
        flex-shrink: 0;
        @media (max-width: 767px) {
          margin-bottom: 8px;
        }
        img {
          @include contain;
        }
      }
      &-text {
        flex-grow: 1;
      }
      &-title {
        padding-right: 48px;
        @include h4;
        margin-bottom: 8px;
        @media (max-width: 767px) {
          padding-right: 0;
        }
      }
      &-label {
        min-height: 31px;
        display: flex;
        align-items: center;
        padding: 5px 10px;
        background-color: #F9F9F9;
        border-radius: 8px;
        @include text-normal;
        &s {
          display: flex;
          flex-wrap: wrap;
          gap: $gap;
          @media (max-width: 767px) {
            gap: 8px;
          }
        }
      }
      &-review {
        position: absolute;
        top: 8px;
        right: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 36px;
        height: 36px;
        border-radius: 8px;
        background-color: #F9F9F9;
        svg {
          width: 20px;
          height: 20px;
          fill: $grey;
          transition: all $transition;
        }
        &:hover {
          svg {
            fill: #FCD34D;
          }
        }
        &_filled {
          svg {
            fill: #FCD34D;
          }
        }
      }
    }
    &-details {
      margin-top: 16px;
      padding: 16px 8px;
      &-title {
        @include h4;
        margin-bottom: 12px;
      }
      &-item {
        display: grid;
        grid-template-columns: 180px auto;
        gap: 12px;
        @include text-small;
        line-height: 1.71;
        @media (max-width: 767px) {
          display: block;
        }
        &:not(:last-child) {
          margin-bottom: 8px;
        }
        &-title {
          color: $grey;
          @media (max-width: 767px) {
            margin-bottom: 4px;
          }
        }
      }
    }
  }
  &__bonus {
    padding: 0;
    &-step {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 16px;
      flex-shrink: 0;
      padding-top: 10px;
      @media (max-width: 767px) {
        &:first-child {
          margin-left: 16px;
        }
        &:last-child {
          margin-right: 15px;
        }
      }
      &s {
        display: flex;
        gap: 24px;
        @media (max-width: 767px) {
          margin: 0 -16px;
          overflow-x: auto;
          -ms-overflow-style: none;
          scrollbar-width: none;
          &::-webkit-scrollbar {
            width: 0;
            height: 0;
          }
        }
      }
      &_active {
        .account__bonus-step {
          &-number {
            color: $orange;
          }
          &-arrow {
            svg {
              fill: $orange;
            }
          }
          &-title {
            border-color: $orange;
            color: $dark;
          }
        } 
      }
      &-block {
        position: relative;
      }
      &-arrow {
        position: absolute;
        top: -9px;
        left: -6px;
        width: 35px;
        height: 75px;
        &_up {
          top: -12px;
          left: -8px;
          width: 50px;
          height: 64px;
        }
        svg {
          width: 100%;
          height: 100%;
          fill: #7C828E;
        }
      }
      &-circle {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        background-color: rgba(#EEEEEE, .4);
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        position: relative;
        z-index: 1;
      }
      &-number {
        @include h4;
        line-height: 1;
        color: $grey;
      }
      &-progress {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        background-color: rgba($orange, .4);
        z-index: -1;
      }
      &-title {
        height: 22px;
        display: flex;
        align-items: center;
        padding: 0 5px;
        border: 1px solid $grey;
        border-radius: 8px;
        @include text-small;
        line-height: 1;
        color: $grey;
      }
    }
  }
  &__deposit {
    &-button {
      align-self: flex-start;
      @media (max-width: 374px) {
        width: 100%;
        padding: 0 10px;
      }
    }
  }
  &__history {
    table {
      width: 100%;
      border-collapse: collapse;
    }
    tr {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: $gap;
      border-radius: 8px;
      @media (max-width: 767px) {
        gap: 3px;
      }
      &:not(:last-child) {
        margin-bottom: 3px;
      }
      &:nth-child(odd) {
        background-color: #F5F6FF;
      }
    }
    td {
      height: 41px;
      display: flex;
      justify-content: center;
      align-items: center;
      @include text-small;
      @media (max-width: 767px) {
        padding: 0 3px;
      }
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}