.about-detail {
  &__heading {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: $gap;
    align-items: flex-end;
    padding-bottom: 16px;
    @media (max-width: 767px) {
      display: block;
    }
  }
  &__title {
    max-width: 475px;
    @include h2;
    line-height: 1.2;
    text-transform: uppercase;
    @media (max-width: 1279px) {
      min-width: 384px;
    }
    @media (max-width: 767px) {
      min-width: auto;
      margin-bottom: 16px;
    }
  }
  &__description {
    margin-left: auto;
    max-width: 500px;
    @include text-small;
    color: $grey;
  }
  &__img {
    width: 100%;
    height: 620px;
    img {
      @include cover;
      @media (max-width: 1279px) {
        object-position: 88%;
      }
    }
  }
}