.filter {
  // transition: all $transition;
  // &_fixed {
  //   position: fixed;
  //   left: 0;
  //   width: 100%;
  //   z-index: 100;
  //   padding: 12px 16px;
  //   background-color: #F5F5F7;
  // }
  &__body {
    display: flex;
    align-items: center;
    gap: 10px;
    height: 53px;
    padding: 7px;
    background-color: $white;
    border: 1px solid #F1F1F1;
    border-radius: 19px;
    position: relative;
    @media (max-width: 1279px) {
      display: inline-flex;
      margin-right: -24px;
    }
    @media (max-width: 1024px) {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      padding-right: 0;
      width: calc(100% + 24px);
    }
    @media (max-width: 767px) {
      margin-right: -16px;
      width: calc(100% + 16px);
    }
  }
  &__button {
    height: 100%;
    display: inline-flex;
    align-items: center;
    flex-shrink: 0;
    gap: 8px;
    padding: 4px 10px;
    background-color: $dark;
    border-radius: 8px;
    @include text-normal;
    line-height: 1;
    color: $white;
    transition: all $transition;
    &_active {
      background-color: $main-blue;
      .filter__button-arrow {
        transform: rotate(-180deg);
      }
    }
    &-img {
      display: none;
      @media (max-width: 767px) {
        display: block;
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        svg {
          width: 100%;
          height: 100%;
          fill: $white;
          stroke: none;
        }
      }
    }
    &-arrow {
      display: flex;
      flex-shrink: 0;
      width: 10px;
      height: 10px;
      transition: all $transition;
      @media (max-width: 767px) {
        display: none;
      }
      svg {
        width: 100%;
        height: 100%;
        stroke: $white;
        stroke-width: 2px;
      }
    }
  }
  &__item {
    input:checked {
      ~ .label__text {
        background-color: $light-orange;
        border-color: $orange;
        color: $orange;
      }
    }
    .label {
      &__box {
        display: none;
      }
      &__text {
        min-height: 37px;
        display: flex;
        align-items: center;
        padding: 4px 9px;
        background-color: #F9F9F9;
        border: 1px solid #F9F9F9;
        border-radius: 8px;
        transition: all $transition;
      }
    }
  }
  &__list {
    flex-grow: 1;
    display: flex;
    gap: 10px;
    @media (max-width: 1279px) {
      overflow-x: auto;
      padding-right: 7px;
    }
    .filter__item {
      flex: 1 1 auto;
      @media (max-width: 1279px) {
        flex: 0 0 auto;
      }
      .label {
        &__text {
          width: 100%;
          justify-content: center;
        }
      }
    }
  }
  &__block {
    padding: 12px 0 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    @include text-normal;
    @media (max-width: 374px) {
      @include text-small;
    }
  }
  &__find {
    color: $grey;
  }
  &__reset {
    display: flex;
    align-items: center;
    gap: 2px;
    line-height: 1;
    transition: all $transition;
    &:hover {
      color: $main-blue-hover;
      svg {
        stroke: $main-blue-hover;
      }
    }
    svg {
      width: 24px;
      height: 24px;
      stroke: $dark;
      stroke-width: 2px;
      transition: all $transition;
    }
  }
  &__dropdown {
    display: none;
    position: absolute;
    top: calc(100% + 4px);
    left: 0;
    width: 100%;
    padding: 11px;
    background-color: $white;
    border: 1px solid $light-grey;
    border-radius: 16px;
    z-index: 1;
    box-shadow: 0 4px 40px rgba(#878787, .25);
    @media (max-width: 1279px) {
      display: block;
      position: fixed;
      top: auto;
      bottom: 0;
      left: 0;
      width: 100%;
      // height: 100%;
      height: 100vh;
      height: 100dvh;
      border: none;
      border-radius: 0;
      padding: 0;
      // padding: 120px 0 0;
      box-shadow: none;
      z-index: 500;
      transform: translateY(-150%);
      background: rgba($dark, .2);
      transition: all .4s ease;
      &_active {
        transform: translateY(0%);
      }
    }
    &-inner {
      @media (max-width: 1279px) {
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 16px 12px 12px;
        // padding: 0 12px 12px;
        // border-radius: 12px 12px 0 0;
        background-color: $light-grey;
      }
    }
    &-body {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 8px;
      @media (max-width: 1279px) {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        overflow-x: hidden;
        overflow-y: auto;
      }
    }
    &-heading {
      display: none;
      @media (max-width: 1279px) {
        display: block;
        margin-bottom: 16px;
        .filter__block {
          padding: 0;
        }
      }
    }
    &-line {
      width: 100%;
      display: block;
      height: 28px;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 64px;
        height: 4px;
        background-color: $white;
        border-radius: 10px;
      }
    }
    &-group {
      max-height: 268px;
      display: flex;
      flex-direction: column;
      gap: 12px;
      padding: 15px;
      border: 1px solid #F1F1F1;
      border-radius: 8px;
      @media (max-width: 1279px) {
        max-height: none;
        background-color: $white;
      }
      &:first-child, &:nth-child(3) {
        grid-row: span 2;
        max-height: 388px;
        @media (max-width: 1279px) {
          max-height: none;
        }
      }
      &-title {
        @include h3;
        flex-shrink: 0;
      }
    }
    &-list {
      flex-grow: 1;
      display: flex;
      flex-wrap: wrap;
      align-content: flex-start;
      gap: 12px;
      overflow-y: auto;
      @media (max-width: 1279px) {
        flex-grow: 0;
        overflow: visible;
      }
      &::-webkit-scrollbar {
        width: 4px;
      }
      &::-webkit-scrollbar-track {
        background: $light-grey;
        border-radius: 30px;
      }
      &::-webkit-scrollbar-thumb {
        width: 100%;
        background: $blue;
        border-radius: 30px;
      }
    }
    .filter__item {
      @media (max-width: 1279px) {
        .label__text {
          @include text-small;
        }
      }
    }
    &-bottom {
      padding-top: 8px;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 8px;
      @media (max-width: 1279px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 767px) {
        grid-template-columns: repeat(1, 1fr);
      }
    }
    &-button {
      grid-column: 4;
      @media (max-width: 1279px) {
        grid-column: 1;
      }
    }
    &-close {
      display: none;
      @media (max-width: 1279px) {
        display: flex;
      }
    }
  }
}