.card {
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 12px;
  background-color: $white;
  border-radius: 20px;
  position: relative;
  .tag {
    position: absolute;
    top: 12px;
    right: 12px;
  }
  &__heading {
    padding-right: 50px;
    &-group {
      display: flex;
      flex-wrap: wrap;
      gap: 1px 8px;
      &:not(:last-child) {
        margin-bottom: 1px;
        @media (max-width: 1279px) {
          margin-bottom: 3px;
        }
        @media (max-width: 767px) {
          margin-bottom: 5px;
        }
      }
    }
  }
  &__title {
    @include h4;
  }
  &__rating {
    display: flex;
    align-items: center;
    align-self: center;
    gap: 3px;
    line-height: 1;
    svg {
      width: 18px;
      height: 18px;
    }
  }
  &__composition, &__sort {
    @include text-small;
    @media (max-width: 767px) {
      @include label;
    }
  }
  &__sort {
    color: $grey;
  }
  &__img {
    width: 100%;
    height: 191px;
    margin-top: auto;
    @media (max-width: 1279px) {
      height: 152px;
    }
    img {
      @include contain;
    }
  }
  &__label {
    display: flex;
    align-items: center;
    min-height: 37px;
    padding: 5px 10px;
    border-radius: 8px;
    background-color: $light-orange;
    @include text-normal;
    color: $orange;
    @media (max-width: 767px) {
      @include text-small;
    }
    &s {
      display: flex;
      flex-wrap: wrap;
      gap: 8px;
    }
  }
  &__text {
    margin-bottom: -3px;
    @media (max-width: 1279px) {
      margin-bottom: -2px;
    }
  }
  &__description {
    min-height: 36px;
    @include text-small;
    margin-bottom: 12px;
  }
  &__info {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    @media (max-width: 1279px) {
      gap: 11px;
    }
    &-item {
      flex: 1;
      @include label;
    }
    &-title {
      margin-bottom: 4px;
      @media (max-width: 1279px) {
        margin-bottom: 2px;
      }
    }
    &-dot {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: #E9E9E9;
      &_active {
        background-color: $main-blue;
      }
      &s {
        display: flex;
        gap: 2px;
        margin-top: 9px;
        @media (max-width: 1279px) {
          margin-top: 8px;
        }
      }
    }
    &-subtitle {
      color: $grey;
    }
  }
  &__bottom {
    &-group {
      display: flex;
      justify-content: space-between;
      gap: 8px;
      &:not(:last-child) {
        margin-bottom: 12px;
      }
      @media (max-width: 1279px) {
        &:last-child {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
        }
      }
      @media (max-width: 767px) {
        &:last-child {
          display: flex;
        }
      }
      @media (max-width: 374px) {
        &:last-child {
          flex-wrap: wrap;
        }
      }
    }
  }
  &__favorites {
    width: 48px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 50%;
    background-color: $light-grey;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    svg {
      width: 24px;
      height: 24px;
      fill: transparent;
      transition: all $transition;
    }
    &_active {
      svg {
        fill: $orange;
      }
    }
  }
  &__pack {
    display: flex;
    width: 100%;
    height: 48px;
    padding: 2px;
    background-color: $light-grey;
    border-radius: 56px;
    &-item {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 5px 2px 2px;
      border-radius: inherit;
      // transition: all $transition;
      transition: color $transition;
      @media (max-width: 1279px) {
        padding-top: 8px;
      }
      &_active {
        background-color: $dark;
        color: $white;
      }
    }
    &-title {
      display: flex;
      align-items: center;
      gap: 5px;
      @include label;
    }
    &-discount {
      color: $orange;
    }
    &-price {
      font-size: 15px;
    }
  }
  &__block {
    width: 165px;
    flex-shrink: 0;
    @media (max-width: 1279px) {
      width: 100%;
      flex-shrink: 1;
    }
    @media (max-width: 767px) {
      width: 117px;
      flex-shrink: 0;
    }
    @media (max-width: 374px) {
      width: 100%;
      height: 48px;
    }
    .card__button {
      width: 100%;
    }
    .counter {
      display: none;
    }
    &_active {
      .card__button {
        display: none;
      }
      .counter {
        display: flex;
      }
    }
  }
}