.blog-detail {
  padding-bottom: 48px;
  @media (max-width: 767px) {
    padding-bottom: 32px;
  }
  @media (max-width: 1279px) {
    overflow: hidden;
  }
  &__img {
    width: 100%;
    height: 445px;
    border-radius: 12px;
    overflow: hidden;
    position: relative;
    @include gradient;
    margin-bottom: 16px;
    @media (max-width: 1279px) {
      margin-left: -24px;
      margin-right: -24px;
      width: auto;
      border-radius: 0;
    }
    @media (max-width: 767px) {
      margin-left: -16px;
      margin-right: -16px;
      height: 315px;
    }
    &:before {
      z-index: unset;
    }
    img {
      @include cover;
    }
  }
  &__body {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__content {
    grid-column: span 2;
    padding-right: 54px;
    @media (max-width: 1279px) {
      padding-right: 0;
      margin-bottom: 32px;
    }
  }
  &__title {
    @include h2;
    line-height: 1.2;
    margin-bottom: 16px;
  }
  &__description {
    @include text-normal;
    line-height: 1.312;
    color: $grey;
    p {
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }
  &__cards {
    margin: 16px 0;
  }
  &__slider {
    @media (max-width: 767px) {
      overflow: visible;
    }
  }
  &__filter {
    padding: 16px;
    background-color: $white;
    border-radius: 16px;
    border: 1px solid #F1F1F1;
    margin-bottom: 16px;
    &-title {
      @include h3;
      margin-bottom: 12px;
    }
    &-list {
      display: flex;
      flex-wrap: wrap;
      gap: 16px;
      @media (max-width: 767px) {
        gap: 8px;
      }
    }
    &-item {
      display: flex;
      align-items: center;
      height: 31px;
      padding: 0 10px;
      background-color: #F9F9F9;
      border-radius: 8px;
      @include text-normal;
      line-height: 1;
      transition: all $transition;
      @media (max-width: 767px) {
        height: auto;
        min-height: 31px;
        padding-top: 5px;
        padding-bottom: 5px;
      }
      &_active {
        background-color: $dark;
        color: $white;
      }
    }
  }
  &__similar {
    &-link {
      @include h2;
      margin-bottom: 16px;
    }
    &-content {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      gap: 6px;
      @media (max-width: 1279px) {
        grid-template-columns: repeat(2, 1fr);
        gap: $gap;
      }
      @media (max-width: 767px) {
        grid-template-columns: repeat(1, 1fr);
      }
      .blog-card {
        height: 315px;
        &_wide {
          grid-column: span 1;
          padding: 16px;
          @media (max-width: 1279px) {
            grid-column: span 2;
          }
          @media (max-width: 767px) {
            grid-column: span 1;
          }
        }
        &__title {
          @include h3;
        }
      }
    }
  }
}