.merch-card {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: $white;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  .tag {
    position: absolute;
    top: 12px;
    right: 12px;
  }
  &__img {
    width: 100%;
    height: 310px;
    img {
      @include cover;
    }
  }
  &__body {
    height: 100%;
    padding: 12px;
    display: flex;
    flex-direction: column;
    @media (max-width: 767px) {
      height: auto;
    }
  }
  &__heading {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 12px;
  }
  &__title {
    @include h4;
  }
  &__rating {
    display: flex;
    align-items: center;
    align-self: center;
    gap: 3px;
    line-height: 1;
    svg {
      width: 18px;
      height: 18px;
    }
  }
  &__info {
    display: flex;
    gap: 24px;
    margin-bottom: 16px;
    &-item {
      min-width: 85px;
      @include label;
    }
    &-title {
      margin-bottom: 2px;
    }
    &-text {
      color: $grey;
    }
  }
  &__bottom {
    margin-top: auto;
    &-group {
      display: flex;
      justify-content: space-between;
      gap: 8px;
      &:not(:last-child) {
        margin-bottom: 12px;
      }
      &:last-child {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
      }
      &:only-child {
        display: flex;
        @media (max-width: 374px) {
          flex-wrap: wrap;
          .merch-card__pack {
            width: calc(100% - 56px);
          }
        }
      }
    }
  }
  &__favorites {
    width: 48px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 50%;
    background-color: $light-grey;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    svg {
      width: 24px;
      height: 24px;
      fill: transparent;
      transition: all $transition;
    }
    &_active {
      svg {
        fill: $orange;
      }
    }
  }
  &__pack {
    display: flex;
    width: 100%;
    height: 48px;
    padding: 2px;
    background-color: $light-grey;
    border-radius: 56px;
    &-item {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 5px;
      padding: 4px 2px 2px;
      border-radius: inherit;
      @include text-normal;
      transition: color $transition;
      // @media (max-width: 1279px) {
      //   padding-top: 8px;
      // }
    }
    &-discount {
      color: $orange;
    }
  }
  &__block {
    width: 100%;
    @media (max-width: 374px) {
      height: 48px;
    }
    .merch-card__button {
      width: 100%;
    }
    .counter {
      display: none;
    }
    &_active {
      .merch-card__button {
        display: none;
      }
      .counter {
        display: flex;
      }
    }
  }
}