.link {
  display: inline-flex;
  align-items: center;
  gap: 12px;
  @include h1;
  transition: all $transition;
  &:hover {
    color: $main-blue-hover;
    svg {
      stroke: $main-blue-hover;
    }
  }
  svg {
    width: 32px;
    height: 32px;
    stroke: $dark;
    stroke-width: 2px;
    transition: all $transition;
  }
}