.basket {
  padding-bottom: 48px;
  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $gap;
    align-items: flex-start;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__body {
    grid-column: span 7;
    @media (max-width: 1279px) {
      margin-bottom: 24px;
    }
  }
  &__box {
    padding: 16px;
    background-color: $white;
    border-radius: 24px;
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }
  &__item {
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: center;
    gap: 8px;
    padding: 7px;
    border: 1px solid #F1F1F1;
    border-radius: 12px;
    @media (max-width: 767px) {
      align-items: flex-start;
    }
    &:not(:last-child) {
      margin-bottom: $gap;
    }
    &-img {
      grid-row: span 2;
      width: 80px;
      height: 82px;
      @media (max-width: 767px) {
        grid-row: span 1;
      }
      img {
        @include contain;
      }
    }
    &-heading {
      display: flex;
      justify-content: space-between;
      gap: $gap;
    }
    &-title {
      @include h4;
      margin-bottom: 8px;
    }
    &-price {
      display: flex;
      align-items: center;
      gap: 8px;
      @include text-normal;
      &-old {
        font-size: 13px;
        color: $grey;
        text-decoration: line-through;
      }
    }
    &-delete {
      width: 22px;
      height: 22px;
      &:hover {
        svg {
          fill: $main-blue-hover;
        }
      }
      svg {
        width: 100%;
        height: 100%;
        fill: $grey;
        transition: all $transition;
      }
    }
    &-bottom {
      height: 48px;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: $gap;
      @media (max-width: 767px) {
        grid-column: span 2;
        grid-template-columns: repeat(1, 1fr);
        gap: 12px;
        height: auto;
        padding: 6px 0;
      }
      &-group {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: $gap;
        @media (max-width: 767px) {
          gap: 8px;
          height: 48px;
        }
        &:only-child {
          grid-template-columns: repeat(4, 1fr);
          grid-column: 1 / span 4;
          @media (max-width: 767px) {
            grid-template-columns: repeat(2, 1fr);
            grid-column: 1;
          }
          .counter {
            grid-column: 4;
            @media (max-width: 767px) {
              grid-column: 2;
            }
          }
        }
      }
      .counter {
        grid-column: 2;
        @media (max-width: 767px) {
          &:only-child {
            grid-column: 1 / span 2;
          }
        }
      }
    }
  }
  &__subtitle {
    font-size: 20px;
    line-height: 1.5;
    margin-bottom: 16px;
  }
  &__info {
    margin-top: 16px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap 12px;
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
      gap: 12px;
    }
  }
  &__recipient {
    &-filter {
      display: flex;
      align-items: center;
      gap: 24px;
    }
    &-block {
      display: none;
      &_active {
        display: block;
      }
    }
  }
  &__delivery {
    &-filter {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 16px;
    }
  }
  &__address {
    width: 100%;
    padding-left: 28px;
    display: none;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    &_active {
      display: flex;
    }
    &-info {
      width: 100%;
      display: none;
      grid-template-columns: repeat(3, 1fr);
      gap: $gap 12px;
      &_active {
        display: grid;
      }
      @media (max-width: 767px) {
        grid-template-columns: repeat(1, 1fr);
        gap: 12px;
      }
    }
  }
  &__payment {
    &-filter {
      display: flex;
      gap: 24px;
    }
    &-block {
      display: none;
      &_active {
        display: block;
      }
    }
  }
  &__deposit {
    margin-top: 16px;
    width: max-content;
    padding: 9px 12px;
    background-color: #F5F5F7;
    border-radius: 12px;
  }
  &__order {
    grid-column: span 5;
    &-heading {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: $gap;
      @include h3;
      line-height: 1.5;
      color: $main-blue;
      margin-bottom: 10px;
      @media (max-width: 1279px) {
        font-size: 24px;
      }
      @media (max-width: 767px) {
        font-size: 24px;
      }
    }
    &-info {
      margin-bottom: 34px;
      &-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: $gap;
        @include text-normal;
        line-height: 1.5;
        position: relative;
        z-index: 1;
        &::before {
          content: '';
          position: absolute;
          bottom: 7px;
          left: 0;
          width: 100%;
          height: 1px;
          border-top: 1px dotted $light-grey;
          z-index: -1;
        }
        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }
      &-title, &-value {
        background-color: $white;
      }
      &-title {
        padding-right: 10px;
      }
      &-value {
        padding-left: 10px;
      }
    }
  }
  &__promo {
    position: relative;
    margin-bottom: 24px;
    input {
      height: 42px;
      padding-left: 140px;
      background-color: $white;
      border-color: #F5F5F7;
      @include text-small;
      &:focus, &:active {
        border-color: $main-blue;
      }
    }
    .select2 {
      &-block {
        position: absolute;
        top: 4px;
        left: 4px;
        bottom: 4px;
        width: 121px;
        z-index: 1;
      }
      &-selection {
        height: 34px !important;
        background-color: $dark !important;
        border-radius: 8px !important;
        &__rendered {
          max-width: calc(100% - 18px);
          padding: 0 8px !important;
          line-height: 34px !important;
          transition: all $transition;
          color: $white !important;
          @media (max-width: 1279px) {
            display: block !important;
            height: auto !important;
            line-height: 34px !important;
            white-space: nowrap !important;
          }
        }
        &__arrow {
          right: 8px !important;
          width: 16px !important;
          height: 16px !important;
          background: url(../img/content/arrow-dropdown-white.svg) 50% / contain no-repeat;
        }
      }
      &-container--open {
        .select2-selection {
          background-color: $main-blue !important;
        }
      }
    }
  }
  .form__button {
    margin-bottom: 24px;
  }
}