.blog-all {
  padding-bottom: 48px;
  @media (max-width: 1023px) {
    overflow: hidden;
  }
  .tab {
    margin-bottom: 16px;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
    .blog-card {
      height: 315px;
      padding: 24px;
      &__title {
        @include h3;
        line-height: 1.2;
      }
    }
  }
  &__bottom {
    margin-top: 48px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(1, 1fr);
      gap: 16px;
      justify-items: center;
    }
    @media (max-width: 767px) {
      margin-top: 32px;
    }
  }
  &__more {
    grid-column: 2;
    width: 100%;
    @media (max-width: 1279px) {
      grid-column: 1;
      max-width: 429px;
    }
  }
}