.social {
  display: inline-flex;
  gap: 8px;
  &__item {
    display: flex;
    width: 32px;
    height: 32px;
    flex-shrink: 0;
    border-radius: 50%;
    overflow: hidden;
    &:hover {
      svg {
        fill: $main-blue-hover;
      }
    }
    svg {
      width: 100%;
      height: 100%;
      fill: $main-blue;
      transition: all $transition;
    }
  }
}