.footer {
  padding: 60px 0;
  background-color: $white;
  @media (max-width: 1279px) {
    padding: 24px 0;
  }
  @media (max-width: 767px) {
    padding: 16px 0;
  }
  .logo {
    display: inline-block;
    width: 88px;
    height: 34px;
    margin-bottom: 24px;
    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__nav {
    grid-column: span 4;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      margin-bottom: 24px;
    }
    @media (max-width: 767px) {
      gap: 36px;
    }
    &-title {
      @include h3;
      margin-bottom: 16px;
    }
  }
  &__list {
    li {
      &:not(:last-child) {
        margin-bottom: 12px;
      }
    }
    a {
      transition: all $transition;
      &:hover {
        color: $main-blue-hover;
      }
    }
  }
  &__info {
    grid-column: span 5;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: $gap;
    align-content: space-between;
    @media (max-width: 1279px) {
      margin-bottom: 24px;
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
      gap: 12px;
    }
    &-item {
      &:nth-child(even) {
        justify-self: flex-end;
        width: 225px;
        @media (max-width: 1279px) {
          width: auto;
          justify-self: unset;
        }
      }
      &:nth-child(n+2) {
        align-self: flex-end;
        @media (max-width: 1279px) {
          align-self: unset;
        }
      }
    }
    &-title {
      @include text-normal;
      color: $grey;
      margin-bottom: 8px;
      @media (max-width: 1279px) {
        @include text-small;
      }
    }
    &-text {
      width: 105%;
      @include h4;
      @media (max-width: 1279px) {
        @include text-normal;
        width: auto;
      }
    }
    &-link {
      transition: all $transition;
      &:hover {
        color: $main-blue-hover;
      }
    }
  }
  &__bottom {
    grid-column: span 3;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    gap: 6px;
    @include text-small;
    color: $grey;
    @media (max-width: 1279px) {
      margin: 0;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
      gap: 16px;
      @include label;
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
    &-group {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      gap: 20px;
      justify-items: flex-end;
      @media (max-width: 1279px) {
        display: flex;
        justify-items: flex-start;
        align-items: center;
        gap: 16px;
      }
      @media (max-width: 767px) {
        gap: 12px;
        flex-wrap: wrap;
        &:last-child {
          flex-direction: column;
          align-items: flex-start;
        }
      }
    }
  }
  &__developed {
    display: inline-flex;
    align-items: center;
    gap: 4px;
    transition: all $transition;
    flex-shrink: 0;
    &:hover {
      color: $main-blue-hover;
    }
    svg {
      width: 38px;
      height: auto;
    }
  }
}