.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($dark, .2);
  z-index: 510;
  overflow-y: auto;
  overflow-x: hidden;
  &_active {
    display: block;
  }
  &__wrap {
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 16px;
  }
  &__content {
    width: 100%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    gap: $gap;
    padding: 16px;
    background-color: $white;
    border-radius: 24px;
    border: 1px solid #F1F1F1;
    position: relative;
    z-index: 1;
  }
  &__filter {
    height: 48px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 4px;
    background-color: $light-grey;
    border-radius: 60px;
    &-item {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 6px;
      border-radius: inherit;
      transition: color $transition;
      &_active {
        background-color: $dark;
        color: $white;
      }
    }
  }
  &__block {
    display: none;
    &_active {
      display: block;
    }
  }
  &__error {
    @include text-small;
    color: $orange;
    text-align: center;
  }
  &__verification {
    display: none;
    &-title {
      @include text-small;
      color: $grey;
      text-align: center;
      letter-spacing: -.01em;
      margin-bottom: 16px;
      b {
        color: $dark;
      }
    }
    &-block {
      display: flex;
      flex-direction: column;
      align-items: center;
      @include text-small;
      color: $grey;
      letter-spacing: -.01em;
      span {
        color: $main-blue;
      }
    }
    &-send {
      color: $main-blue;
    }
    .form {
      &__content {
        margin: 0 auto;
        width: 266px;
        grid-template-columns: repeat(4, 1fr);
        gap: 6px;
        input {
          height: 62px;
          border-radius: 50%;
          text-align: center;
          color: $main-blue;
          &:valid {
            background-color: $light-blue-2;
          }
        }
      }
    }
  }
  &__img {
    width: 183px;
    height: 192px;
    margin: 0 auto;
    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    @include h3;
  }
  &__description {
    margin-top: 8px;
    @include text-normal;
    color: $grey;
  }
  &__rating {
    &-title {
      @include text-small;
      color: $grey;
      margin-bottom: 4px;
    }
    &-item {
      width: 38px;
      height: 38px;
      &s {
        display: flex;
        gap: 5px;
      }
      svg {
        width: 100%;
        height: 100%;
      }
    }
  }
  &_payment {
    .form {
      &__content {
        grid-template-columns: repeat(2, 1fr);
      }
      .label {
        &_wide {
          grid-column: span 2;
        }
      }
    }
  }
}