.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 500;
  background-color: $white;
  transition: all $transition, padding-right 0s;
  &::after {
    content: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #F1F1F1;
    z-index: -1;
  }
  &_scroll {
    &:after {
      content: '';
    }
  }
  &_active {
    &:after {
      content: '';
    }
  }
  .container {
    @media (max-width: 1279px) {
      padding: 0;
    }
  }
  &__inner {
    @media (max-width: 1279px) {
      padding: 18px 24px 11px;
      background-color: $white;
    }
    @media (max-width: 767px) {
      padding: 14px 16px 12px;
    }
  }
  &__body {
    padding: 18px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 1279px) {
      padding: 0 0 18px;
    }
    @media (max-width: 767px) {
      padding-bottom: 14px;
    }
  }
  .logo {
    display: inline-flex;
    width: 88px;
    height: 34px;
    flex-shrink: 0;
    @media (max-width: 374px) {
      width: 60px;
    }
    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__content {
    margin: 0 32px;
    > .header__info {
      display: none;
      @media (max-width: 1279px) {
        display: block;
      }
    }
    @media (max-width: 1439px) {
      margin: 0 16px;
    }
    @media (max-width: 1279px) {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      height: 100vh;
      height: 100dvh;
      margin: 0;
      z-index: -2;
      transform: translateY(-150%);
      transition: all .4s ease;
      padding: 142px 16px 30px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      background: $white;
      overflow-x: hidden;
      overflow-y: auto;
      // @media (max-width: 767px) {
      //   padding: 87px 16px 16px;
      // }
      &_active {
        transform: translateY(0%);
      }
      .nav {
        margin-bottom: 32px;
        // @media (max-width: 767px) {
        //   margin-bottom: 56px;
        // }
      }
    }
  }
  &__action {
    display: flex;
    gap: 17px;
    margin-left: auto;
    &-basket {
      &-count {
        position: absolute;
        top: -2px;
        right: -9px;
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background-color: $main-blue;
        display: flex;
        justify-content: center;
        align-items: center;
        @include label;
        color: $white;
        line-height: 1;
      }
    }
    @media (max-width: 1439px) {
      gap: 12px;
      margin-left: 0;
    }
    @media (max-width: 1279px) {
      gap: 16px;
    }
    &-item {
      min-width: 60px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 2px;
      @media (max-width: 1365px) {
        min-width: auto;
      }
      // @media (max-width: 1279px) {
      //   min-width: auto;
      // }
      &_login {
        color: $main-blue;
        svg {
          fill: $main-blue;
        }
      }
      &:hover {
        color: $main-blue-hover;
        svg {
          fill: $main-blue-hover;
        }
      }
    }
    &-img {
      width: 24px;
      height: 24px;
      position: relative;
      svg {
        width: 100%;
        height: 100%;
        transition: all $transition;
      }
    }
    &-title {
      @include text-small;
      transition: all $transition;
      @media (max-width: 1365px) {
        display: none;
      }
      // @media (max-width: 1279px) {
      //   display: none;
      // }
    }
  }
  &__info {
    &-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      gap: 4px;
      min-height: 68px;
      padding: 10px 11px;
      border: 1px solid $light-grey;
      border-radius: 8px;
      font-size: 17px;
      transition: all $transition;
      @media (max-width: 1279px) {
        min-height: auto;
        gap: 8px;
        padding: 0;
        border: none;
        border-radius: 0;
        font-size: 18px;
      }
      &:not(:last-child) {
        margin-bottom: 8px;
        @media (max-width: 1279px) {
          margin-bottom: 28px;
        }
      }
      &:hover:not(div) {
        background-color: $light-grey;
        .header__info-text {
          color: $main-blue-hover;
        }
      }
    }
    &-title {
      @include text-small;
      color: $grey;
      @media (max-width: 1279px) {
        font-size: 15px;
      }
    }
    &-text {
      transition: all $transition;
    }
  }
  &__search {
    position: relative;
    .header__action-item {
      height: 100%;
    }
    @media (max-width: 1279px) {
      display: none;
    }
    .search {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      width: 0;
      opacity: 0;
      pointer-events: none;
      transition: all $transition;
      z-index: 1;
    }
    &_active {
      .search {
        width: 873px;
        opacity: 1;
        pointer-events: all;
        @media (max-width: 1439px) {
          // width: 852px;
          width: 58.5vw;
        }
      }
    }
  }
  &__profile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 3px 8px;
    background-color: $light-grey;
    border-radius: 12px;
    @media (max-width: 1279px) {
      padding: 2px 8px;
    }
    &-name {
      @include text-small;
      margin-bottom: 2px;
      @media (max-width: 1279px) {
        margin-bottom: 0;
        line-height: 1;
      }
    }
    &-info {
      display: flex;
      gap: 10px;
      align-items: center;
      @include label;
      color: $main-blue;
      @media (max-width: 1279px) {
        line-height: 1;
      }
    }
    &-bonus {
      position: relative;
      display: flex;
      align-items: center;
      gap: 2px;
      line-height: 1;
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: calc(100% + 5px);
        transform: translateY(-50%);
        width: 1px;
        height: 50%;
        background-color: #D6D6E1;
      }
    }
  }
  &__bottom {
    display: none;
    @media (max-width: 1279px) {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
    }
  }
}