.counter {
  width: 100%;
  height: 100%;
  padding: 5px 12px;
  background-color: $light-grey;
  border-radius: 56px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  @media (max-width: 767px) {
    gap: 2px;
  }
  &__button {
    width: 24px;
    height: 24px;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all $transition;
    &.disabled {
      pointer-events: none;
      svg {
        stroke: #A4A4BA;
      }
    }
    &:hover {
      svg {
        stroke: $main-blue;
      }
    }
    svg {
      width: 100%;
      height: 100%;
      stroke: $dark;
      stroke-width: 2px;
      transition: all $transition;
    }
  }
  &__number {
    width: 40px !important;
    height: 22px !important;
    border: none;
    background: none;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    padding: 0 !important;
    text-align: center;
    pointer-events: none;
    @include h4;
    color: $dark;
  }
}