.catalog {
  padding: 12px 0 48px;
  @media (max-width: 1279px) {
    overflow: hidden;
  }
  @media (max-width: 767px) {
    padding-bottom: 32px;
  }
  &__heading {
    margin-bottom: 12px;
  }
  &__title {
    @include h2;
    margin-bottom: 12px;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__bottom {
    margin-top: 48px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(1, 1fr);
      gap: 16px;
      justify-items: center;
    }
    @media (max-width: 767px) {
      margin-top: 32px;
    }
  }
  &__more {
    grid-column: 2;
    width: 100%;
    @media (max-width: 1279px) {
      grid-column: 1;
      max-width: 429px;
    }
  }
}