.blog-card {
  display: flex;
  align-items: flex-end;
  width: 100%;
  height: 231px;
  padding: 16px;
  border-radius: 16px;
  overflow: hidden;
  position: relative;
  z-index: 1;
  color: $white;
  @include gradient;
  @media (max-width: 1279px) {
    height: 250px;
  }
  @media (max-width: 1023px) {
    height: 150px;
  }
  &_wide {
    height: auto;
    grid-column: span 2;
    grid-row: span 2;
    padding: 32px;
    .blog-card__title {
      @include h2;
    }
    @media (max-width: 1279px) {
      order: -1;
      grid-row: span 1;
      height: 400px;
    }
    @media (max-width: 1023px) {
      height: 350px;
    }
    @media (max-width: 767px) {
      grid-column: span 1;
      padding: 16px;
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
    overflow: hidden;
    img {
      @include cover;
    }
  }
  &__title {
    @include h4;
    line-height: 1.1;
  }
}