.button {
  display: flex;
  justify-content: center;
  align-items: center;
  // width: 165px;
  height: 48px;
  padding: 0 16px;
  background-color: $main-blue;
  border-radius: 56px;
  transition: all $transition;
  @include text-normal;
  color: $white;
  &:hover {
    background-color: $main-blue-hover;
  }
  &:active {
    background-color: $dark;
  }
  &_white {
    background-color: $white;
    color: $dark;
    &:hover {
      background-color: #E3E3E7;
    }
    &:active {
      background-color: $white;
    }
  }
  &_grey {
    background-color: $light-grey;
    color: $dark;
    &:hover {
      background-color: $main-blue-hover;
      color: $white;
    }
    &:active {
      background-color: $dark;
      color: $white;
    }
  }
  &_disabled {
    background-color: $light-grey;
    color: $grey;
    cursor: not-allowed;
    &:hover, &:active {
      background-color: $light-grey;
      color: $grey;
    }
  }
}