.pagination {
  display: inline-flex;
  align-items: center;
  gap: 12px;
  padding: 0 12px;
  margin-left: auto;
  @media (max-width: 1279px) {
    padding: 12px;
    margin: 0;
  }
  &__button {
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all $transition;
    &-prev {
      svg {
        transform: rotate(180deg);
      }
    }
    &:hover {
      svg {
        stroke: $main-blue-hover;
      }
    }
    svg {
      width: 100%;
      height: 100%;
      stroke: $dark;
      stroke-width: 2px;
      transition: all $transition;
    }
    &_disabled {
      pointer-events: none;
      svg {
        stroke: $grey;
      }
    }
  }
  &__list {
    display: flex;
  }
  &__item {
    width: 48px;
    height: 21px;
    display: flex;
    justify-content: center;
    align-items: center;
    @include text-normal;
    border-left: 1px solid #E5E5E5;
    border-right: 1px solid #E5E5E5;
    transition: all $transition;
    &:not(:first-child) {
      border-left: none;
    }
    &:not(.pagination__item_active):hover {
      color: $main-blue-hover;
    }
    &_empty {
      color: #A3A3A3;
    }
    &_active {
      color: $main-blue;
    }
  }
}