.shop-card {
  display: flex;
  flex-direction: column;
  &__img {
    width: 100%;
    height: 157px;
    border-radius: 12px 12px 0 0;
    overflow: hidden;
    img {
      @include cover;
    }
  }
  &__body {
    flex-grow: 1;
    padding: 0 11px;
    background-color: $white;
    border: 1px solid $white;
    border-radius: 0 0 12px 12px;
  }
  &__info {
    display: flex;
    justify-content: space-between;
    gap: 12px;
    padding: 11px 0;
    &:not(:last-child) {
      border-bottom: 1px solid $light-blue-2;
    }
  }
  &__title {
    padding: 2px 0;
    @include text-small;
    color: $grey;
  }
  &__text {
    text-align: right;
    align-self: center;
    @include text-normal;
  }
  &__worktime {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 7px;
    &:not(:last-child) {
      margin-bottom: 4px;
    }
    span {
      min-width: 52px;
      text-align: left;
      color: $grey;
    }
  }
}