.coffee-shop {
  &__map {
    width: 100%;
    height: 683px;
    @media (max-width: 767px) {
      height: 560px;
    }
    [class*=-ground-pane] {
      filter: grayscale(1);
      mix-blend-mode: difference;
    }
    // .ymaps-2-1-79-ground-pane {
    //   filter: grayscale(1);
    //   mix-blend-mode: difference;
    // }
  }
  &__content {
    padding: 48px 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      padding: 32px 0;
      grid-template-columns: repeat(1, 1fr);
    }
  }
}