.tag {
  &__item {
    width: 44px;
    height: 44px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $light-blue-2;
    border: 1px solid $blue;
    @include label;
    line-height: 1;
    color: $main-blue;
    &_discount {
      background-color: $light-orange;
      border-color: $orange;
      color: $orange;
    }
    &:not(:last-child) {
      margin-bottom: 6px;
    }
  }
}