.product {
  padding-bottom: 24px;
  @media (max-width: 1279px) {
    overflow: hidden;
  }
  @media (max-width: 767px) {
    padding-bottom: 16px;
  }
  &__inner {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $gap;
    align-items: flex-start;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__gallery {
    height: 552px;
    grid-column: span 7;
    display: flex;
    flex-direction: row-reverse;
    @media (max-width: 1279px) {
      margin-bottom: 20px;
    }
    @media (max-width: 767px) {
      height: auto;
      flex-direction: column;
    }
  }
  &__slider {
    height: 100%;
    border-radius: 24px;
    overflow: hidden;
    @media (max-width: 767px) {
      width: 100%;
      height: 266px;
    }
    &-item {
      background-color: $white;
      img {
        @include cover;
      }
    }
  }
  &__thumbs {
    width: 177px;
    height: 100%;
    flex-shrink: 0;
    margin-right: 24px;
    @media (max-width: 767px) {
      width: 100%;
      height: 110px;
      margin-right: 0;
      margin-top: 12px;
    }
    &-item {
      border-radius: 12px;
      overflow: hidden;
      border: 2px solid transparent;
      transition: border $transition;
      cursor: pointer;
      &.swiper-slide-thumb-active {
        border-color: $main-blue;
      }
      img {
        border-radius: inherit;
        @include cover;
      }
    }
  }
  &__info {
    grid-column: span 5;
    position: sticky;
    top: 100px;
    right: 0;
    border: 1px solid #F1F1F1;
    border-radius: 24px;
    overflow: hidden;
    transition: all $transition;
    @media (max-width: 1279px) {
      margin-bottom: 16px;
      position: static;
    }
    &_static {
      position: relative;
      top: 0;
    }
    .card {
      min-height: 552px;
      padding: 23px;
      border-radius: 0;
      gap: 24px;
      @media (max-width: 1279px) {
        gap: 16px;
        min-height: auto;
      }
      @media (max-width: 767px) {
        padding: 11px;
      }
      &__heading {
        padding-right: 0;
        &-group {
          gap: 3px 12px;
          @media (max-width: 767px) {
            gap: 3px 8px;
          }
          &:not(:last-child) {
            margin-bottom: 3px;
            @media (max-width: 767px) {
              margin-bottom: 1px;
            }
          }
        }
      }
      &__title {
        @include h2;
      }
      &__rating {
        gap: 3px;
        svg {
          width: 24px;
          height: 24px;
        }
      }
      &__composition, &__sort {
        @include text-normal;
        @media (max-width: 767px) {
          @include text-small;
        }
      }
      &__text {
        margin-top: -8px;
        @media (max-width: 1279px) {
          margin: 0;
          display: flex;
          flex-direction: column;
          gap: 16px;
        }
      }
      &__description {
        padding: 10px;
        background-color: $light-grey;
        border-radius: 8px;
        @include text-normal;
        line-height: 1.5;
        margin-bottom: 24px;
        @media (max-width: 1279px) {
          order: +1;
          margin: 0;
        }
        @media (max-width: 767px) {
          @include text-small;
        }
      }
      &__info {
        @media (max-width: 1279px) {
          justify-content: flex-start;
          gap: 30px;
        }
        @media (max-width: 767px) {
          justify-content: space-between;
          gap: 11px;
        }
        &-item {
          @include text-normal;
          @media (max-width: 1279px) {
            flex: unset;
          }
          @media (max-width: 767px) {
            font: 1px;
            @include label;
          }
        }
        &-title {
          margin-bottom: 0;
          @media (max-width: 767px) {
            margin-bottom: 3px;
          }
        }
        &-dot {
          width: 10px;
          height: 10px;
          @media (max-width: 767px) {
            width: 8px;
            height: 8px;
          }
          &s {
            margin-top: 6px;
            @media (max-width: 767px) {
              margin-top: 8px;
            }
          }
        }
      }
      &__bottom {
        margin-top: auto;
      }
    }
    .merch-card {
      min-height: 552px;
      height: auto;
      border-radius: 0;
      @media (max-width: 1279px) {
        min-height: auto;
      }
      &__body {
        flex-grow: 1;
        padding: 23px;
        @media (max-width: 767px) {
          padding: 11px;
        }
      }
      &__heading {
        gap: 12px;
        margin-bottom: 16px;
      }
      &__title {
        @include h2;
      }
      &__rating {
        svg {
          width: 24px;
          height: 24px;
        }
      }
      &__description {
        padding: 10px;
        background-color: $light-grey;
        border-radius: 8px;
        @include text-normal;
        line-height: 1.5;
        margin-bottom: 24px;
        @media (max-width: 767px) {
          @include text-small;
          margin-bottom: 16px;
        }
      }
      &__info {
        margin-bottom: 24px;
        @media (max-width: 767px) {
          margin-bottom: 16px;
        }
        &-item {
          @include text-normal;
          @media (max-width: 767px) {
            @include label;
          }
        }
      }
    }
  }
  .tab {
    grid-column: span 7;
    @media (max-width: 1439px) {
      overflow-x: auto;
      &::-webkit-scrollbar {
        height: 4px;
      }
      &::-webkit-scrollbar-track {
        background: $light-grey;
        border-radius: 30px;
      }
      &::-webkit-scrollbar-thumb {
        height: 4px;
        background: $main-blue;
      }
    }
    @media (max-width: 1279px) {
      margin-bottom: 16px;
    }
    &__list {
      min-width: 100%;
      @media (max-width: 1279px) {
        min-width: auto;
      }
    }
    &__item {
      flex: 1;
      justify-content: center;
    }
  }
  &__content {
    grid-column: span 7;
    &_wide {
      grid-column: span 12;
    }
  }
  &__buyers {
    background-color: $white;
    border-radius: 12px;
    overflow: hidden;
    &-heading {
      .product__buyers-item {
        color: $grey;
      }
    }
    &-item {
      display: grid;
      grid-template-columns: repeat(7, 1fr);
      gap: $gap;
      border-top: 1px solid #F1F1F1;
      font-size: 15px;
      @media (max-width: 1279px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 767px) {
        gap: 0;
      }
    }
    &-size {
      grid-column: span 4;
      @media (max-width: 1279px) {
        grid-column: span 1;
      }
    }
    &-price {
      grid-column: span 3;
      border-left: 1px solid #F1F1F1;
      @media (max-width: 1279px) {
        grid-column: span 1;
      }
    }
    &-size, &-price {
      height: 39px;
      display: flex;
      align-items: center;
      padding: 5px 20px;
    }
  }
  &__blog {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
    min-height: 280px;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(3, 1fr);
      min-height: auto;
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
    &-item {
      padding: 10px;
      background-color: $white;
      line-height: 1.5;
      border-radius: 8px;
    }
    &-title {
      @include h4;
      margin-bottom: 10px;
    }
    &-description {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      @include text-small;
      color: #3E4759;
      margin-bottom: 12px;
    }
    &-link {
      @include text-small;
      color: $main-blue;
      text-decoration: underline;
    }
  }
  &__reviews {
    &-item {
      padding: 10px;
      background-color: $white;
      border-radius: 8px;
      &:not(:last-child) {
        margin-bottom: 12px;
      }
    }
    &-title {
      @include h4;
      line-height: 1.5;
      margin-bottom: 10px;
    }
    &-description {
      @include text-small;
      line-height: 1.5;
      color: #3E4759;
      margin-bottom: 10px;
    }
    &-info {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      gap: 8px;
      @media (max-width: 767px) {
        gap: 4px 8px;
      }
    }
    &-author {
      display: flex;
      align-items: center;
      gap: 8px;
      @media (max-width: 767px) {
        width: 100%;
      }
      &-photo {
        width: 20px;
        height: 20px;
        flex-shrink: 0;
        border-radius: 50%;
        overflow: hidden;
        margin: 2px;
        img {
          @include cover;
        }
      }
      &-name {
        @include text-small;
      }
    }
    &-rating {
      display: flex;
      gap: 2px;
      @media (max-width: 767px) {
        gap: 4px;
      }
      &-item {
        width: 20px;
        height: 20px;
        @media (max-width: 767px) {
          width: 18px;
          height: 18px;
        }
        svg {
          width: 100%;
          height: 100%;
        }
      }
    }
    &-date {
      @include label;
      line-height: 1;
      color: $grey;
    }
    &-bottom {
      display: flex;
      justify-content: space-between;
      margin-top: 12px;
      gap: 24px;
      @media (max-width: 767px) {
        flex-direction: column;
        gap: 16px;
        align-items: center;
      }
    }
    &-more {
      width: 100%;
      @media (max-width: 1279px) {
        max-width: 429px;
      }
    }
  }
  &__map {
    width: 100%;
    height: 712px;
    border-radius: 16px;
    overflow: hidden;
    @media (max-width: 1279px) {
      height: 560px;
    }
    @media (max-width: 767px) {
      width: auto;
      margin: 0 -16px;
      border-radius: 0;
    }
    &-wrap {
      position: relative;
      @media (max-width: 1279px) {
        padding-top: 60px;
      }
    }
    > ymaps {
      border-radius: 16px;
      overflow: hidden;
      @media (max-width: 767px) {
        border-radius: 0;
        overflow: visible;
      }
    }
    [class*=-ground-pane] {
      filter: grayscale(1);
      mix-blend-mode: difference;
    }
    // .ymaps-2-1-79-ground-pane {
    //   filter: grayscale(1);
    //   mix-blend-mode: difference;
    // }
  }
  &__shop {
    position: absolute;
    top: 16px;
    right: 16px;
    bottom: 0;
    width: 375px;
    display: flex;
    flex-direction: column;
    padding: 12px 12px 0;
    background-color: $white;
    border-radius: 24px 24px 0 0;
    @media (max-width: 1279px) {
      position: static;
      padding: 0;
      width: 100%;
      display: block;
      background: none;
      border-radius: 0;
    }
    &-button {
      height: 40px;
      &:not(.product__shop-button_active) {
        background-color: transparent;
        color: $dark;
      }
      &s {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        padding: 4px;
        border-radius: 56px;
        background-color: $light-grey;
        margin-bottom: 12px;
        @media (max-width: 1279px) {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          background-color: $white;
        }
      }
    }
    &-content {
      flex-grow: 1;
      overflow-y: auto;
      padding-right: 12px;
      padding-bottom: 12px;
      @media (max-width: 1279px) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 16px;
        overflow: visible;
        flex-grow: 0;
        padding: 12px 0 0;
      }
      @media (max-width: 767px) {
        grid-template-columns: repeat(1, 1fr);
      }
      &::-webkit-scrollbar {
        width: 4px;
      }
      &::-webkit-scrollbar-track {
        background: $light-grey;
        border-radius: 30px;
        margin: 0 0 12px;
      }
      &::-webkit-scrollbar-thumb {
        width: 100%;
        background: $main-blue;
        border-radius: 30px;
      }
      .shop-card {
        &:not(:last-child) {
          margin-bottom: 12px;
          @media (max-width: 1279px) {
            margin-bottom: 0;
          }
        }
        &__body {
          border-color: #F1F1F1;
        }
      }
    }
  }
}