.bonus {
  padding-bottom: 48px;
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__block {
    padding: 23px;
    background-color: $white;
    border: 1px solid #F1F1F1;
    border-radius: 24px;
    overflow: hidden;
    &:last-child {
      grid-column: span 2;
      @media (max-width: 1279px) {
        grid-column: span 1;
      }
    }
  }
  &__title {
    @include h3;
    margin-bottom: 16px;
  }
  &__list {
    counter-reset: num;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 12px;
    &:not(:last-child) {
      margin-bottom: 16px;
    }
    &-number {
      display: flex;
      justify-content: center;
      align-items: center;
      align-self: flex-start;
      width: 30px;
      height: 30px;
      flex-shrink: 0;
      border: 1px solid $orange;
      border-radius: 5px;
      @include text-normal;
      line-height: 1;
      position: relative;
      &::before {
        counter-increment: num;
        content: counter(num);
      }
    }
    &-text {
      @include text-small;
    }
  }
  &__img {
    padding: 0;
    width: 100%;
    @media (max-width: 1279px) {
      height: 229px;
      img {
        transform: scale(1.1);
      }
    }
    @media (max-width: 767px) {
      height: 400px;
      img {
        transform: none;
      }
    }
    img {
      @include contain;
    }
  }
}