.faq {
  padding-bottom: 24px;
  @media (max-width: 1023px) {
    overflow: hidden;
  }
  @media (max-width: 767px) {
    padding-bottom: 16px;
  }
  .tab {
    margin-bottom: 16px;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__block {
    grid-column: span 2;
    padding: 7px 23px;
    background-color: $white;
    border-radius: 24px;
    border: 1px solid #F1F1F1;
  }
  &__item {
    &:not(:last-child) {
      border-bottom: 1px solid #F9F9F9;
    }
    &-heading {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 32px;
      padding: 16px 0 15px;
      @media (max-width: 767px) {
        gap: 12px;
      }
      &_active {
        .faq__item {
          &-title {
            color: $main-blue;
          }
          &-action {
            background-color: $light-blue-2;
            &::before {
              background-color: $main-blue;
            }
            &::after {
              opacity: 0;
            }
          }
        }
      }
    }
    &-title {
      @include h3;
      transition: all $transition;
    }
    &-action {
      width: 46px;
      height: 46px;
      flex-shrink: 0;
      background-color: $light-grey;
      border-radius: 8px;
      position: relative;
      transition: all $transition;
      align-self: flex-start;
      @media (max-width: 767px) {
        width: 32px;
        height: 32px;
      }
      &::before, &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 24px;
        height: 2px;
        background-color: $dark;
        transition: all $transition;
        @media (max-width: 767px) {
          width: 14px;
          height: 2px;
        }
      }
      &::after {
        width: 2px;
        height: 24px;
        @media (max-width: 767px) {
          width: 2px;
          height: 14px;
        }
      }
    }
    &-dropdown {
      display: none;
      padding-bottom: 15px;
    }
    &-description {
      max-width: 700px;
      @include text-normal;
      line-height: 1.5;
      color: $grey;
      @media (max-width: 1279px) {
        max-width: 604px;
      }
      p {
        &:not(:last-child) {
          margin-bottom: 16px;
        }
      }
    }
  }
}