.nav {
  .header-desktop {
    @media (max-width: 1279px) {
      display: none;
    }
  }
  .header-mobile {
    @media (min-width: 1280px) {
      display: none;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 24px;
    @media (max-width: 1439px) {
      gap: 12px;
    }
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__item {
    display: flex;
    position: relative;
    @media (max-width: 1279px) {
      display: block;
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
    &:first-child {
      .nav__dropdown {
        left: -120px;
        transform: none;
        &-list {
          width: 324px;
        }
      }
    }
    &_active {
      .nav__link {
        color: $main-blue-hover;
        svg {
          stroke: $main-blue-hover;
          transform: rotate(-180deg);
        }
      }
    }
  }
  &__link {
    display: flex;
    align-items: center;
    gap: 3px;
    @include text-normal;
    position: relative;
    transition: all $transition;
    z-index: 1;
    @media (min-width: 1280px) {
      &:hover {
        color: $main-blue-hover;
        svg {
          stroke: $main-blue-hover;
          transform: rotate(-180deg);
        }
      }
    }
    @media (max-width: 1279px) {
      width: max-content;
    }
    svg {
      flex-shrink: 0;
      width: 16px;
      height: 16px;
      stroke: $dark;
      transition: all $transition;
    }
  }
  &__dropdown {
    display: none;
    position: absolute;
    top: calc(100% + 14px);
    left: 50%;
    transform: translateX(-50%);
    width: max-content;
    min-width: 350px;
    max-width: 1018px;
    padding: 12px;
    background-color: $white;
    border: 1px solid $light-grey;
    border-radius: 16px;
    @media (max-width: 1279px) {
      position: static;
      transform: none;
      width: auto;
      min-width: auto;
      max-width: none;
      padding: 12px 12px 0;
      background: none;
      border: none;
      border-radius: 0;
    }
    &::before {
      content: '';
      position: absolute;
      bottom: 100%;
      left: 0;
      width: 100%;
      height: 20px;
      opacity: 0;
      @media (max-width: 1279px) {
        content: none;
      }
    }
    &-inner {
      display: flex;
    }
    &-list {
      > li {
        .nav__dropdown-sublist {
          @media (min-width: 1280px) {
            display: none !important;
          }
        }
      }
      a {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: $gap;
        min-height: 68px;
        padding: 10px 11px;
        border: 1px solid $light-grey;
        border-radius: 8px;
        font-size: 17px;
        transition: all $transition;
        @media (max-width: 1279px) {
          width: max-content;
          justify-content: flex-start;
          min-height: auto;
          padding: 0;
          border: none;
          border-radius: 0;
          gap: 2px;
          font-size: 16px;
          &:has(.soon) {
            gap: 12px;
            color: $grey;
          }
        }
        &.active {
          &:not(:has(.soon)) {
            background-color: $light-grey;
            .arrow {
              background-color: $white;
            }
            @media (max-width: 1279px) {
              background: none;
              color: $main-blue-hover;
              .arrow {
                background: none;
                transform: rotate(-90deg);
                svg {
                  stroke: $main-blue-hover;
                }
              }
            }
          }
        }
        @media (min-width: 1280px) {
          &:not(:has(.soon)) {
            &:hover {
              background-color: $light-grey;
              .arrow {
                background-color: $white;
              }
            }
          }
        }
        .arrow {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 36px;
          height: 36px;
          border-radius: 50%;
          background-color: $light-blue-2;
          transition: all $transition;
          @media (max-width: 1279px) {
            width: 16px;
            height: 16px;
            border-radius: 0;
            background: none;
            transform: rotate(90deg);
            svg {
              width: 100%;
              height: 100%;
              stroke: $dark;
              stroke-width: 2px;
            }
          }
          svg {
            width: 18px;
            height: 18px;
            stroke: $main-blue;
            stroke-width: 3px;
          }
        }
        .soon {
          height: 32px;
          display: flex;
          align-items: center;
          padding: 0 10px;
          background-color: $light-orange;
          border-radius: 8px;
          @include text-small;
          color: $orange;
          @media (max-width: 1279px) {
            height: 22px;
            padding: 0 8px;
          }
        }
      }
    }
    li {
      &:not(:last-child) {
        margin-bottom: 8px;
        @media (max-width: 1279px) {
          margin-bottom: 12px;
        }
      }
    }
    &-title {
      display: flex;
      flex-direction: column;
      gap: 4px;
      span {
        @include text-small;
        color: $grey;
        @media (max-width: 1279px) {
          display: none;
        }
      }
    }
    &-block {
      @media (max-width: 1279px) {
        display: none;
      }
    }
    &-item {
      display: none;
      gap: 8px;
      margin-left: 8px;
      &.active {
        display: flex;
      }
    }
    &-group {
      width: 326px;
      padding: 15px;
      border: 1px solid $light-grey;
      border-radius: 8px;
      &-title {
        @include h3;
        margin-bottom: 12px;
      }
    }
    &-sublist {
      @media (max-width: 1279px) {
        display: none;
        padding: 12px 20px 0;
      }
      li {
        &:not(:last-child) {
          margin-bottom: 12px;
        }
      }
      a {
        transition: all $transition;
        &:hover {
          color: $main-blue-hover;
        }
      }
    }
  }
}