.form {
  &__content {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: $gap;
  }
  &__bottom {
    margin-top: 16px;
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  &__button {
    width: 100%;
  }
  &__privacy, &__box {
    padding: 9px 12px;
    background-color: $light-grey;
    border-radius: 12px;
  }
  &__privacy {
    .label__text {
      @include label;
    }
  }
}

.label {
  display: flex;
  flex-direction: column;
  gap: 4px;
  cursor: pointer;
  position: relative;
  z-index: 1;
  &_wide {
    grid-column: span 3;
    @media (max-width: 767px) {
      grid-column: span 1;
    }
  }
  &__title {
    order: -1;
    @include text-small;
    color: $grey;
    position: relative;
    // &::after {
    //   content: '*';
    //   color: $main-blue;
    //   margin-left: 4px;
    //   opacity: 0;
    // }
  }
}

input, textarea {
  width: 100%;
  height: 46px;
  border: 1px solid transparent;
  border-radius: 12px;
  background-color: $light-grey;
  padding: 11px;
  appearance: none;
  @include text-normal;
  color: $dark;
  transition: all $transition;
  outline: none;
  cursor: pointer;
  &:hover {
    background-color: #E3E3E7;
  }
  &:focus, &:active {
    outline: none;
    border-color: $main-blue;
  }
  &:checked {
    ~ .label {
      &__box {
        background-color: $main-blue;
        &::after {
          opacity: 1;
        }
      }
    }
  }
  &::placeholder {
    color: $grey;
  }
  &.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
  // &[required] ~ .label__title::after {
  //   opacity: 1;
  // }
}

textarea {
  resize: none;
}

.label {
  &__box {
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    background-color: $white;
    border-radius: 3px;
    overflow: hidden;
    position: relative;
    transition: all $transition;
    &::after {
      content: '';
      position: absolute;
      inset: 0;
      opacity: 0;
      background: url(../img/sprites/check.svg) 50% / contain no-repeat;
      transition: all $transition;
    }
  }
  &__text {
    @include text-normal;
  }
  &__description {
    @include text-small;
    color: $grey;
    transition: all $transition;
  }
  &_checkbox {
    flex-direction: row;
    align-items: center;
    gap: 10px;
    -webkit-tap-highlight-color: transparent;
  }
  &_radio {
    align-items: flex-start;
    gap: 8px;
    input:checked {
      ~ .label {
        &__box {
          background-color: $orange;
          
        }
        &__text {
          .label__description {
            color: $dark;
          }
        }
      }
    }
    .label {
      &__box {
        background-color: $light-grey;
        border-radius: 50%;
        &::after {
          inset: 5px;
          opacity: 0;
          background: $white;
          border-radius: 50%;
        }
      }
      &__text {
        display: flex;
        flex-direction: column;
        gap: 6px;
      }
    }
  }
}