.pre-catalog {
  @include section;
  @media (max-width: 1279px) {
    overflow: hidden;
  }
  &__link, &__title {
    margin-bottom: 16px;
    @media (max-width: 767px) {
      margin-bottom: 8px;
    }
  }
  &__title {
    @include h1;
  }
  &__slider {
    @media (max-width: 1279px) {
      overflow: visible;
    }
    &-wrapper {
      @media (max-width: 767px) {
        flex-wrap: wrap;
        gap: 12px;
      }
    }
  }
  &__item {
    height: auto;
  }
}