@mixin cover {
  height: 100%;
  object-fit: cover;
}

@mixin contain {
  height: 100%;
  object-fit: contain;
}

@mixin pposans {
  font-family: 'PP Object Sans', sans-serif;
}

@mixin section {
  padding: 24px 0;
  @media (max-width: 767px) {
    padding: 16px 0;
  }
}

@mixin gradient {
  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 60%;
    background: linear-gradient(180deg, rgba(#2c2c38, 0) 0%, #2c2c38 100%);
    z-index: -1;
    pointer-events: none;
  }
  
}

@mixin h1 {
  font-size: 44px;
  @media (max-width: 1279px) {
    font-size: 36px;
  }
  @media (max-width: 767px) {
    font-size: 32px;
  }
}

@mixin h2 {
  font-size: 36px;
  @media (max-width: 1279px) {
    font-size: 30px;
  }
  @media (max-width: 767px) {
    font-size: 24px;
  }
}

@mixin h3 {
  font-size: 24px;
  @media (max-width: 1279px) {
    font-size: 20px;
  }
  @media (max-width: 767px) {
    font-size: 18px;
  }
}

@mixin h4 {
  font-size: 18px;
  @media (max-width: 767px) {
    font-size: 16px;
  }
}

@mixin text-normal {
  font-size: 16px;
}

@mixin text-small {
  font-size: 14px;
}

@mixin label {
  font-size: 12px;
}