.breadcrumbs {
  padding: 16px 0;
  @media (max-width: 767px) {
    overflow: hidden;
    .container {
      padding: 0;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    @include text-small;
    color: $grey;
    @media (max-width: 767px) {
      overflow-x: auto;
      -ms-overflow-style: none;
      scrollbar-width: none;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
  }
  &__item {
    position: relative;
    @media (max-width: 767px) {
      &:first-child {
        padding-left: 16px;
      }
      &:last-child {
        padding-right: 16px;
      }
    }
    &:not(:last-child) {
      margin-right: 13px;
      &::after {
        content: '/';
        position: absolute;
        top: 50%;
        left: calc(100% + 4px);
        transform: translateY(-50%);
        pointer-events: none;
      }
    }
  }
  &__link {
    display: flex;
    white-space: nowrap;
  }
}