.burger {
  display: none;
  @media (max-width: 1279px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 42px;
    height: 42px;
    flex-shrink: 0;
    padding: 5px;
    background-color: $light-blue-2;
    border-radius: 8px;
    position: relative;
    z-index: 1;
    span {
      width: 22px;
      height: 2px;
      background-color: $main-blue;
      transition: all $transition;
      &:not(:last-child) {
        margin-bottom: 6px;
      }
    }
    &_active {
      span {
        margin: 0;
        &:nth-child(1) {
          transform: rotate(45deg);
          margin-bottom: -1px;
        }
        &:nth-child(2) {
          display: none;
        }
        &:nth-child(3) {
          transform: rotate(-45deg);
          margin-top: -1px;
        }
      }
    }
  }
}