.about {
  margin: 24px 0;
  position: relative;
  z-index: 1;
  color: $white;
  @include gradient;
  @media (max-width: 767px) {
    margin: 16px 0;
    &::before {
      height: 100%;
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
    overflow: hidden;
    img {
      @include cover;
    }
  }
  &__content {
    padding: 60px 0;
    min-height: 653px;
    display: flex;
    align-items: flex-end;
    @media (max-width: 1279px) {
      padding: 24px 0;
    }
    @media (max-width: 767px) {
      min-height: 550px;
      padding: 16px 0;
    }
  }
  &__title {
    @include h1;
    margin-bottom: 16px;
  }
  &__description {
    max-width: 630px;
    @include h4;
    line-height: 1.5;
  }
}