.not-found {
  padding: 73px 0;
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  &__img {
    width: 157px;
    height: 213px;
    margin-bottom: 16px;
    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__text {
    max-width: 379px;
  }
  &__title {
    @include h2;
    margin-bottom: 8px;
  }
  &__subtitle {
    @include h4;
    color: $grey;
    margin-bottom: 24px;
  }
}