.blog {
  padding: 24px 0 48px;
  @media (max-width: 767px) {
    padding: 16px 0 32px;
  }
  &__link {
    margin-bottom: 24px;
    @media (max-width: 767px) {
      margin-bottom: 8px;
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
      gap: 8px;
    }
  }
}