.hero {
  padding: 48px 0 24px;
  @media (max-width: 767px) {
    padding: 8px 0 16px;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
      gap: 8px;
    }
  }
  &__item {
    display: flex;
    align-items: flex-end;
    width: 100%;
    height: 231px;
    padding: 16px;
    border-radius: 16px;
    overflow: hidden;
    position: relative;
    z-index: 1;
    color: $white;
    @include gradient;
    @media (max-width: 1279px) {
      height: 250px;
    }
    @media (max-width: 1023px) {
      height: 150px;
    }
    &_wide {
      height: auto;
      grid-column: span 2;
      grid-row: span 2;
      padding: 32px;
      @media (max-width: 1279px) {
        grid-row: span 1;
        height: 400px;
      }
      @media (max-width: 1023px) {
        height: 350px;
      }
      @media (max-width: 767px) {
        padding: 16px;
        grid-column: span 1;
      }
      .hero__item-title {
        @include h2;
      }
    }
    &-img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -2;
      overflow: hidden;
      img {
        @include cover;
      }
    }
    &-title {
      @include h3;
      line-height: 1.1;
      @media (max-width: 1279px) {
        @include h4;
      }
    }
  }
}