.tab {
  @media (max-width: 1023px) {
    margin-left: -24px;
    margin-right: -24px;
    overflow-x: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
  }
  @media (max-width: 767px) {
    margin-left: -16px;
    margin-right: -16px;
  }
  &__list {
    display: inline-flex;
    gap: 16px;
    padding: 7px;
    background-color: $white;
    border: 1px solid #F1F1F1;
    border-radius: 16px;
    @media (max-width: 1023px) {
      margin: 0 24px;
    }
    @media (max-width: 767px) {
      margin: 0 16px;
    }
  }
  &__item {
    height: 31px;
    display: flex;
    align-items: center;
    padding: 0 10px;
    background-color: #F9F9F9;
    border-radius: 8px;
    @include text-normal;
    line-height: 1;
    white-space: nowrap;
    transition: all $transition;
    &_active {
      background-color: $dark;
      color: $white;
    }
  }
  &-content {
    display: none;
    &_active {
      display: block;
    }
  }
}